.todo {
  width: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #000000;
  padding: 10px;
  margin: 0 auto;
}
ul {
  padding-inline-start: 0;
}
.button {
  border-radius: 10px;
  height: 40px;
  background-color: yellowgreen;
  cursor: pointer;
}
.text {
  text-align: left;
}
input {
  width: 20px;
  height: 20px;
}
